.nav-container{
    display: flex;
    justify-content: space-between;
    background: rgba(255, 255, 255, 0.881);
    padding: 0 80px;
    box-shadow: 0px 4px 20px 0px rgb(0 0 0 / 16%);
    height: 10vh;

    .profil{
        display: flex;
        align-items: center;

        img{
            height: 50px;
            width: 50px;
            border-radius: 100px;
            margin-right: 10px;
        }
    }

    .nav{
        display: flex;
        align-items: center;
        flex-direction: row;

        a{
            text-decoration: none;
            color: black;
            margin: 30px 60px;
            font-weight: bold;
            transition: 0.2s;

            &:hover{
                text-shadow:  2px 3px 6px rgb(0 0 0 / 25%);
                transform: scale(1.05);
            }
        }
    }
}