.home-container{
    
    .main{
        height: 80vh;
        display: flex;
        justify-content: center;
        align-items: center;

        .text{
            width: 50%;
            text-align: center;

            h1{
                font-size: 5rem;
                margin-bottom: 20px;

                span:last-child{
                    animation: anim1 1s infinite 1s;

                    @keyframes anim1 {
                        from{
                            opacity: 0;                        }
                    } to{
                        opacity: 1;
                    }

                }
            }
            
            p{
                color: rgb(47,79,79);
                font-size: 1.2rem;
            }
        }
    }
}