.contact-container{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    .contact{
        
        background: white;
        display: flex;
        flex-direction: column;
        padding: 30px 20px;
        box-shadow: 3px 3px 10px 0 #0000002e;
        transition: 0.4s;

        &:hover{
            transform: scale(1.05);
            box-shadow: 4px 4px 15px 0 #0000002e;
        }
    
        a{
            text-decoration: none;
            color: black;
            margin: 15px 0;
            transition: 0.2s;
            
    
            &:hover{
                color: gray;
            }
        }

    }
  p{
    background: white;
    height: 30px;
    border-radius: 10px;
    padding: 5px;
    transform: translateY(195px) translateX(5px);
    box-shadow: 4px 4px 15px 0 #0000002e;
  }  
}