.project-container{
    width: 100vw;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    

    .project{
        height: 60%;
        width: 100%;
        display: flex;
        overflow: hidden;
        scroll-behavior: smooth;
        box-shadow: 4px 10px 20px 0px rgb(0 0 0 / 40%);
        .items{

            min-width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 0 100px;
            
            

            .content{

                position: relative;
                background: rgba(255, 255, 255, 0.699);
                padding: 20px;
                text-align: center;
                // box-shadow: 4px 10px 20px 0px rgb(0 0 0 / 40%);
                padding: 10px 200px;
                height: 100%;
                width: 100%;
                .video-container{
                    
                    display: flex;
                    margin-top: 30px;
                    img{
                        height: 50%;
                        width: 50%;
                        
                        border-radius: 20px;
                        box-shadow: 4px 10px 20px 0px rgb(0 0 0 / 40%);
                    }
        
        
                }
                .text{
                    display: flex;
                    justify-content: center;
                    align-items: center;           
                    background: rgba(255, 255, 255, 0.599);
                    margin: auto;
                    padding: 40px;
                    text-align: center;
                    width: 40%;
                    height: 60%;
                    box-shadow: 4px 10px 20px 0px rgb(0 0 0 / 20%);
        
                    
            
                    
                }
        
                a{
                    position: absolute;
                    bottom: 20px;
                    left: 50%;
                    transform: translateX(-50%);
                    text-decoration: none;
                    color: white;
                    padding: 10px;
                    border: solid 1px rgb(47,79,79);
                    border-radius: 20px;
                    background: rgb(47,79,79);
                    box-shadow: 4px 10px 20px 0px rgb(0 0 0 / 20%);
                    transition: 0.2s;
        
                    &:hover{
                        transform: scale(1.02) translateX(-50%);
                    }
                }
            }

        }
    }

    button{
        position: absolute;
        height: 50px;
        width: 50px;
        background: transparent;
        border: none;

        svg{
            stroke: white;
            transition: 0.2s;

            &:hover{
                transform: scale(1.05);
            }
        }
    }

    #prev{
        left: 15px;
    }

    #next{
        right: 15px;
    }
    
}