.form-container{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

    h2{
        font-size: 3rem;
    }

    form{
        display: flex;
        flex-direction: column;
        width: 50%;

        input{
            width: 50%;
            height: 40px;
            border-radius: 20px;
            border: solid 1px white;
            box-shadow: 4px 4px 15px 0 #0000002e;
            margin-bottom: 20px;
            padding: 10px;
            font-family: "font1";
        }
        textarea{
            resize: none;
            border-radius: 20px;
            border: solid 1px white;
            box-shadow: 4px 4px 15px 0 #0000002e;
            height: 200px;
            padding: 10px;
            font-family: "font1";
        }

        input[type=submit]{
            width: 25%;
            margin: 20px auto 50px;
            transition: 0.4s;

            &:hover{
                transform: scale(1.05);

            }

        }

        label{
            margin-bottom: 5px;
        }

    }
    a{
        text-decoration: none;
        color: black;
        transition: 0.2s;

        i{
            margin-left: 5px;
        }

        &:hover{
            transform: scale(1.2);
    }
}

    
}